import React, { Component } from 'react';
import api from "../services/api"

import './css/add.css';
import { Container, Row, Col, Image, ToggleButton } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import { ToggleButtonGroup } from 'react-bootstrap';

class Add extends Component {
    state = {
        name: '',
        at1: 0,
        at2: 0,
        at3: 0,
        description: '',
        affiliation: '',
        spell: '',
        tag: '',
        image: null,
        windows: false,
    }

    handleSubmit = async e => {
        e.preventDefault();
        const data = new FormData();

        data.append('name', this.state.name);
        data.append('image', this.state.image);
        data.append('at1', this.state.at1);
        data.append('at2', this.state.at2);
        data.append('at3', this.state.at3);
        data.append('description', this.state.description);
        data.append('affiliation', this.state.affiliation);
        data.append('spell', this.state.spell);
        data.append('tag', this.state.tag);

        await api.post('create', data);
        console.log("envio");

    }

    handleImageChange = e => {
        console.log(e.target.files[0])
        this.setState({ image: e.target.files[0] });

        var preview = document.getElementById("imageshow");
        var file = document.getElementById("imagem").files[0];
        var reader = new FileReader();

        reader.onloadend = () => { preview.src = reader.result; }

        file ? reader.readAsDataURL(file) : preview.src = "";
    }

    handleChange = e => {
        this.setState({ [e.target.name]: e.target.value });
    }

    handleChangeRadio = val => {
        this.setState({ affiliation: val });
        console.log(val);
    }

    async handleChangeImgShow(event) {

    }



    render() {
        return (

            <Container className="align-items-center" >
                {
                    <Row>

                        <form id="new-card"
                            className="Neumorphism"
                            onSubmit={this.handleSubmit}
                        >
                            <Col sm={4}>
                                <Container className="d-flex justify-content-center" >
                                    <Image id="imageshow"
                                        src='https://via.placeholder.com/350x550'
                                        alt="Card"
                                        thumbnail />
                                </Container>
                                <input type="file" id="imagem" onChange={this.handleImageChange} />
                            </Col>

                            <Col >
                                <Row>
                                    <Col sm={6} >
                                        <Form.Control
                                            type="text"
                                            name="name"
                                            placeholder="Nome"
                                            onChange={this.handleChange}
                                            value={this.state.name}
                                        />

                                    </Col>
                                    <Col sm={6} className="atributs d-flex justify-content-around align-items-center">


                                        <Form.Label>At1:</Form.Label>
                                        <Form.Control
                                            type="number"
                                            name="at1"
                                            placeholder="Atributo 1"
                                            onChange={this.handleChange}
                                            value={this.state.at1}
                                        />
                                        <Form.Label>At2:</Form.Label>
                                        <Form.Control
                                            type="number"
                                            name="at2"
                                            placeholder="Atributo 2"
                                            onChange={this.handleChange}
                                            value={this.state.at2}
                                        />
                                        <Form.Label>At3:</Form.Label>
                                        <Form.Control
                                            type="number"
                                            name="at3"
                                            placeholder="Atributo 3"
                                            onChange={this.handleChange}
                                            value={this.state.at3}
                                        />
                                    </Col>
                                </Row>
                                <Row className="atributs d-flex justify-content-center">
                                    <Col sm={6}>
                                        <Form.Control
                                            type="text"
                                            name="tag"
                                            placeholder="tags"
                                            onChange={this.handleChange}
                                            value={this.state.tag}
                                        />
                                    </Col>
                                    <Col sm={6}>
                                        <ToggleButtonGroup className="d-flex justify-content-center"
                                            type="radio"
                                            name="affiliation"
                                            onChange={this.handleChangeRadio}
                                            value={this.state.affiliation}>
                                            <ToggleButton variant="light" value={"Africano"}>Africano</ToggleButton>
                                            <ToggleButton variant="light" value={"Indigena"}>Indigena</ToggleButton>
                                            <ToggleButton variant="light" value={"Europeu"}>Europeu</ToggleButton>
                                            <ToggleButton variant="light" value={"Neutro"}>Neutro</ToggleButton>
                                        </ToggleButtonGroup>
                                    </Col>
                                </Row>
                                <Row>
                                    <textarea
                                        type="text"
                                        name="spell"
                                        placeholder="Habilidades"
                                        onChange={this.handleChange}
                                        value={this.state.spell}

                                    />

                                    <textarea
                                        type="text"
                                        name="description"
                                        placeholder="Descrição"
                                        onChange={this.handleChange}
                                        value={this.state.description}

                                    />
                                </Row>


                                <button type="submit">Enivar</button>
                            </Col>
                        </form>

                    </Row>
                }
            </Container >
        )
    }
}

export default Add;